import React from 'react';

const SideNav =({props})=>{
	
		return (
		<div id='sideNavContainer'>
		  <span id='open' onClick={props}>&#9776; Kategorie produktów</span>
		  <div id="mySidenav" className='sidenav'>
		    <a onClick={props} id='close' className='shadow-hover'>&times;</a>
		    <a className='shadow-hover' >Odbojniki, stopki samoprzylepne</a>
		    <a className='shadow-hover' >Taśmy, wykroje samoprzylepne</a>
		    <a className='shadow-hover' >Akcesoria światłowodowe</a>
		    <a className='shadow-hover' >Ferryty / rdzenie ferrytowe</a>
		    <a className='shadow-hover' >Tulejki dystansowe</a>
		    <a className='shadow-hover' >Prowadnice, mocowania do PCB</a>
		    <a className='shadow-hover' >Nity, zatrzaski, zaślepki</a>
		    <a className='shadow-hover' >Plastikowe wkręty, śrubki, nakrętki, podkładki</a>
		    <a className='shadow-hover' >Metalowe śrubki z podkładką</a>
		    <a className='shadow-hover' >Akcesoria, uchwyty, osłony kabli</a>
		    <a className='shadow-hover' >Korytka kablowe</a>
		    <a className='shadow-hover' >Koszulki termokurczliwe</a>
		    <a className='shadow-hover' >Przelotki, przepusty kablowe</a>
		    <a className='shadow-hover' >Dławnice kablowe</a>
		    <a className='shadow-hover' >Osłony krawędzi, narożników</a>
		    <a className='shadow-hover' >Opaski zaciskowe</a>
		    <a className='shadow-hover' >Uchwyty do opasek kablowych</a>
		    <a className='shadow-hover' >Filtry, osłony do wentylatorów</a>
		    <a className='shadow-hover' >Podstawki i oprawki pod LED</a>
		    <a className='shadow-hover' >Tulejki, izolatory, osłony</a>
		    <a className='shadow-hover' >Amortyzatory, reduktory wibracji</a>
		    <a className='shadow-hover' >Stopki, nóżki do obudów</a>
		    <a className='shadow-hover' >Stopki meblowe i przemysłowe</a>
		    <a className='shadow-hover' >Podkładki samoprzylepne</a>
		    <a className='shadow-hover' >Pokrętła i uchwyty</a>
		    <a className='shadow-hover' >Zaślepki, uchwyty do szkła</a>
		    <a className='shadow-hover' >Zaślepki i nasadki</a>
		    <a className='shadow-hover' >Wkładki, korki, zaślepki do rur</a>
		    <a className='shadow-hover' >Końcówki, nakładki na rury</a>
		    <a className='shadow-hover' >Osłony rur i kołnierzy</a>
		    <a className='shadow-hover' >Elementy maskujące odporne na wysokie temperatury</a>
		    <a className='shadow-hover' >Osłony zabezpieczające</a>
		    <a className='shadow-hover' >Zabezpieczenia: budowlane, siatki</a>
		    <a className='shadow-hover' >Kółka meblowe</a>
		    <a className='shadow-hover' >Akcesoria meblowe, tapicerskie</a>
		    <a className='shadow-hover' >Uszczelki</a>
		    <a className='shadow-hover' >Narzędzia</a>
		    <a className='shadow-hover' >Metalowe elementy złączne</a>
		    <a className='shadow-hover' >Uchwyty szafek, meblowe</a>
		    <a className='shadow-hover' >Kapturki i osłony na śruby i nity</a>
		    <a className='shadow-hover' >Przyssawki</a>
		    <a className='shadow-hover' >Sprężyny</a>
		  </div>

		  
		</div>
		)
	}

export default SideNav


